.search-bar {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.search-input {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-right: 10px;
  width: 300px;
}

.reputable-sources-tag{
  
  color: #E0E0E0;
}
.search-button {
  padding: 10px 20px;
  background-color: #449eff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 10px;
}

.search-button:hover {
  background-color: #0056b3;
}

.date-range-select {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}